/**
 * Contact page.
 */
import React from "react"
import { Helmet } from "react-helmet"
//import { Link as GatsbyLink } from "gatsby"
//import ContactForm from "../components/contact-form"
import ContactForm from "../components/contactForm"
import { loadReCaptcha, ReCaptcha } from 'react-recaptcha-v3'
import config from '../../gatsby-config'
import '../styles/contact.css'

/**
 * SiteMeta.
 */
const { title, siteUrl, google } = config.siteMetadata

/**
 * Page Meta.
 */
const pageMeta = {
  title: 'Nous joindre',
  description: 'Contenu de la page nous joindre...',
}

/**
 * Sublayout.
 */
const subLayout = {
  container: 'col'
}

/**
 * React component (JSX).
 */
class NousJoindrePage extends React.Component {

  /*
  state = {
    recaptchaToken: ""
  }
  */
 
  /**
   * Load Google reCaptcha in the page.
   */
  componentDidMount() {
    loadReCaptcha(google.recaptchaSiteKey)
  }

  /**
   * Unload Google reCaptcha from the page.
   */
  componentWillUnmount() {

    //const recaptcha = this.state.recaptchaToken
    //console.log("Recaptcha token from unmount: %s", recaptcha)
    
    /**
     * Remove the UI element.
     */
    const nodeBadge = document.querySelector('.grecaptcha-badge')
    if (nodeBadge) {
      document.body.removeChild(nodeBadge.parentNode)
    }
    const chatra = document.querySelector('#chatra')
    if (chatra) {
      chatra.parentElement.removeChild(chatra)
    }

    /**
     * Remove the script element.
     * 
     * @note  Query selector not working.
     */
    const scripts = document.scripts
    for (let elem of scripts) {
      if( elem.src.indexOf('recaptcha/api.js') !== -1) {
        //thisScript = scripts[i]
        elem.parentNode.removeChild(elem)
      } else if( elem.src.indexOf('chatra.io/chatra.js') !== -1) {
        //thisScript = scripts[i]
        elem.parentNode.removeChild(elem)
      }
      /*
      console.log(`elem: ${elem.src}`);
      if (elem.matches('script[src$="www.recaptcha.net"]')) {
        console.log("The archive reference: " + elem.src )
      }
      */
    }
    
    /*
    const scripts = document.scripts
    const scriptSelector = `script[src="https://www.recaptcha.net/recaptcha/api.js?onload=onLoadCaptchaV3Callback&amp;render=${google.recaptchaSiteKey}"]`
    console.log(scriptSelector)
    const nodeScript = document.querySelector(scriptSelector)
    console.log(nodeScript)
    if (nodeScript) {
      document.body.removeChild(nodeScript.parentNode)
    }
    */
    //var scripts = document.getElementsByTagName('script')

    /*
    var scripts = document.scripts
    var thisScript = null
    var i = scripts.length
    while (i--) {
      if( scripts[i].src && (scripts[i].src.indexOf('recaptcha/api.js') !== -1) ) {
        thisScript = scripts[i]
        thisScript.parentNode.removeChild(thisScript);
        break;
      }
    }
    */

    /*
    const script = document.querySelector(thisScript)
    console.log(script)
    if(script) {
      script.remove()
    }
    */
  }

  verifyCallback = (Token) => {
    this.setState({
      recaptchaToken: Token
    })
    
    // Here you will get the final recaptchaToken!!!  
    //console.log(recaptchaToken, "<= your recaptcha token")
  }

  updateToken = () => {
    // you will get a new token in verifyCallback
    this.recaptcha.execute();
  }

  render() {
    return (
    <>
    <Helmet>
      <script type="application/ld+json">
        {`
            {
              "@context": "https://schema.org",
              "@type": "Organization",
              "url": "${siteUrl}",
              "name": "${title}",
              "contactPoint": {
                "@type": "ContactPoint",
                "telephone": "450-431-7253",
                "contactType": "Service à la clientèle"
              }
            }
          `}
      </script>
      <script>
      {`
      window.ChatraSetup = {
          buttonSize: 70,
          colors: {
            buttonText: '#f9f8fa',
            buttonBg: '#ea5600'
        },
        };
        (function(d, w, c) {
            w.ChatraID = 'YR8kBfXZ7t8tm7BRg';
            var s = d.createElement('script');
            w[c] = w[c] || function() {
                (w[c].q = w[c].q || []).push(arguments);
            };
            s.async = true;
            s.src = 'https://call.chatra.io/chatra.js';
            if (d.head) d.head.appendChild(s);
        })(document, window, 'Chatra');
        `}
        </script>
    </Helmet>
    <div id="content" className="container col">
      <div className="content">
        <h1>{pageMeta.title}</h1>
        <ContactForm />
      </div>
      <aside>
        <h2>Téléphone</h2>
        <p><a href="tel:450-431-7253">450-431-7253</a></p>
      </aside>
      <ReCaptcha
        ref={ref => this.recaptcha = ref}
        sitekey={google.recaptchaSiteKey}
        action='action_name'
        verifyCallback={this.verifyCallback}
      />
    </div>
    </>
    )
    }
  }

NousJoindrePage.pageMeta = pageMeta
NousJoindrePage.subLayout = subLayout

export default NousJoindrePage
